/**
 * Example Loader Utility
 * Handles loading and managing example markdown files
 */

class ExampleLoader {
  constructor() {
    this.examples = new Map()
    this.loadExamples()
  }

  async loadExamples() {
    // Load atomic design example using inline content
    const atomicDesignContent = this.getInlineExample('atomic-design.md')
    this.examples.set('atomic-design', {
      id: 'atomic-design',
      title: 'Atomic Design',
      description: 'Sistema de design baseado em componentes',
      content: atomicDesignContent,
      category: 'design-systems'
    })
  }

  async loadExampleFile(filename) {
    try {
      // In a real application, you might fetch from a server
      // For now, we'll inline the content or use dynamic imports
      const response = await fetch(`/src/examples/${filename}`)
      if (!response.ok) {
        throw new Error(`Failed to load example: ${response.status}`)
      }
      return await response.text()
    } catch (error) {
      // Fallback: return inline content if fetch fails
      return this.getInlineExample(filename)
    }
  }

  getInlineExample(filename) {
    const examples = {
      'atomic-design.md': `# Atomic Design

## Conceitos Fundamentais
### Metodologia de Design
- Criada por Brad Frost
- Inspirada na qumica
- Hierarquia de componentes
- Reutilizao e consistncia

### Benefcios
- Escalabilidade
- Manutenibilidade
- Consistncia visual
- Desenvolvimento eficiente
- Documentao clara

## tomos (Atoms)
### Elementos Bsicos
- Botes
- Inputs
- Labels
- Icons
- Tipografia

### Caractersticas
- Menor unidade funcional
- No podem ser decompostos
- Altamente reutilizveis
- Propriedades bsicas
- Estados simples

## Molculas (Molecules)
### Combinaes Simples
- Formulrio de busca
- Card de produto
- Menu de navegao
- Toolbar
- Breadcrumb

### Caractersticas
- Agrupamento de tomos
- Funcionalidade especfica
- Reutilizveis em contextos
- Interface mais complexa

## Organismos (Organisms)
### Sees Complexas
- Header completo
- Footer
- Sidebar
- Lista de produtos
- Formulrio completo

### Caractersticas
- Combinao de molculas
- Sees distintas da interface
- Funcionalidade completa
- Contexto especfico

## Templates
### Estrutura de Pgina
- Layout grid
- Wireframes
- Posicionamento
- Hierarquia visual
- Responsive design

### Caractersticas
- Esqueleto da pgina
- Sem contedo real
- Foco na estrutura
- Guias de layout

## Pginas (Pages)
### Instncias Reais
- Homepage
- Pgina de produto
- Checkout
- Dashboard
- Perfil do usurio

### Caractersticas
- Template com contedo real
- Casos de uso especficos
- Testes de usabilidade
- Validao do design`
    }

    return examples[filename] || '# Exemplo no encontrado\n\nO exemplo solicitado no pde ser carregado.'
  }

  getExample(id) {
    return this.examples.get(id)
  }

  getAllExamples() {
    return Array.from(this.examples.values())
  }

  getExamplesByCategory(category) {
    return this.getAllExamples().filter(example => example.category === category)
  }

  async loadExampleContent(id) {
    const example = this.getExample(id)
    if (!example) {
      throw new Error(`Example with id "${id}" not found`)
    }

    return example.content
  }

  addExample(id, exampleData) {
    this.examples.set(id, {
      id,
      title: exampleData.title,
      description: exampleData.description,
      content: exampleData.content,
      category: exampleData.category || 'custom'
    })
  }

  removeExample(id) {
    return this.examples.delete(id)
  }

  hasExample(id) {
    return this.examples.has(id)
  }

  getExampleMetadata(id) {
    const example = this.getExample(id)
    if (!example) return null

    return {
      id: example.id,
      title: example.title,
      description: example.description,
      category: example.category
    }
  }
}

// Create singleton instance
const exampleLoader = new ExampleLoader()

export default exampleLoader
export { ExampleLoader }
